@charset "UTF-8";

@import "./_common/base";
@import "./layout";
@import "./main";
/* common css */

//기본 스타일
input[type=text],
textarea,
select {
    outline:none;
    @include transition(border-color, .2s);
    &:focus { border-color:$gray2 !important;}
}
::selection { background:#000; color:$white; text-shadow:none; }
::-moz-selection { background:$gray2; color:$white; text-shadow:none; }
img { vertical-align:top; image-rendering:-webkit-optimize-contrast;}
caption { width:1px; height:1px; overflow:hidden; margin:-1px;}

.dimmed {
    width:100%; height:100%; position:fixed; background-color:$black; display:none; top:0; left:0;
    @include opacity(0);
}

//hidden text
.hidden-txt,
legend {width:0; height:0; overflow:hidden; display:inline-block; text-indent:-9999px; line-height:1px; position:absolute;}

strong, h1, h2, h3, h4, h5, h6 {letter-spacing:-3.5px;}
em { font-weight:$bold; }
.donate_btn {
    a { width:190px; padding:20px 0; display:inline-block; background-color:$red; color:#fff; font-size:16px; border-radius:100px; text-align:center; font-weight:$regular; }
}

strong.thick_border {
    position:relative; font-size:28px; letter-spacing:-2px; display:inline-block; z-index:9;
    &:after { width:100%; height:8px; content:""; display:block; position:absolute; bottom:-3px; z-index:-1; background-color:$red; }
}

@include pc-a() {
    strong.thick_border {
        font-size:26px;
    }
}














